	module.exports = "<h2 id=overview>Overview</h2> <p>Because the table component is not used for layout and presents tabular data only, it requires the use of <code>role=&quot;grid&quot;</code>. Expandable table content (<code>.pf-c-table__expandable-content</code>) is placed within a singular <code>&lt;td&gt;</code> per expandable row, that can span multiple columns.</p> <h2 id=role-grid>Role=&quot;grid&quot;</h2> <p>Applying <code>role=&quot;grid&quot;</code> to tables enhances accessible interaction while in table layout, however the responsive, css grid based layout can cause unexpected interactions. Therefore, for css grid layout, it is recommended that <code>role=&quot;grid&quot;</code> be removed.</p> <h2 id=sortable-tables>Sortable Tables</h2> <p>Table columns may shift when expanding/collapsing. To address this, set <code>.pf-m-fit-content</code>, or assign a width <code>.pf-m-width-[width]</code> to the corresponding <code>&lt;th&gt;</code> defining the column or <code>&lt;td&gt;</code> within the column. Width values are <code>[10, 15, 20, 25, 30, 35, 40, 45, 50, 60, 70, 80, 90]</code> or <code>max</code>.</p> <h2 id=table-header-cells>Table Header Cells</h2> <p>By default, all table header cells are set to <code>white-space: nowrap</code>. If a <code>&lt;th&gt;</code>&#39;s content needs to wrap, apply <code>.pf-m-wrap</code>.</p> <h2 id=implementation-support>Implementation Support</h2> <ul> <li>One expandable toggle button, positioned in the first cell of a non-expandable row, preceding an expandable row.</li> <li>One checkbox, positioned in the first or second cell of a non-expandable row.</li> <li>One action button, positioned in the last cell of a non-expandable row.</li> <li>Tabular data.</li> <li>Compact presentation modifier (not compatible with expandable table).</li> </ul> <h2 id=responsive-layout-modifiers>Responsive layout modifiers</h2> <table> <thead> <tr> <th>Class</th> <th>Applied To</th> <th>Outcome</th> </tr> </thead> <tbody> <tr> <td><code>.pf-m-grid-md</code>, <code>.pf-m-grid-lg</code></td> <td><code>.pf-c-table</code></td> <td>Changes tabular layout to responsive, grid based layout at suffixed breakpoint.</td> </tr> <tr> <td><code>.pf-m-grid</code></td> <td><code>.pf-c-table</code></td> <td>Changes tabular layout to responsive, grid based layout. This approach requires JavaScript to set this class at some prescribed viewport width value.</td> </tr> </tbody> </table> <h2 id=extra-modifiers>Extra modifiers</h2> <table> <thead> <tr> <th>Class</th> <th>Applied To</th> <th>Outcome</th> </tr> </thead> <tbody> <tr> <td><code>.pf-m-wrap</code></td> <td><code>&lt;th&gt;</code>, <code>&lt;td&gt;</code></td> <td>Modifies content to wrap.</td> </tr> </tbody> </table> ";

